import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  TextField,
  Popper,
  Paper,
  List,
  ListItem,
  ListItemText,
  CircularProgress,
} from '@mui/material';

const Autocomplete = ({ setInputSymbol }) => {
  const [query, setQuery] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const [loading, setLoading] = useState(false);
  const API_KEY = 'csch419r01qpohrs4bc0csch419r01qpohrs4bcg'; // Replace with your Finnhub API key
  const API_URL = 'https://finnhub.io/api/v1/search'; // Finnhub stock symbol search API

  useEffect(() => {
    if (query.length > 1) {
      const fetchSuggestions = async () => {
        setLoading(true);
        try {
          const response = await axios.get(`${API_URL}?q=${query}&token=${API_KEY}`);
          setSuggestions(response.data.result); // Store the suggestions
        } catch (error) {
          console.error('Error fetching suggestions:', error);
        }
        setLoading(false);
      };

      fetchSuggestions();
    } else {
      setSuggestions([]);
    }
  }, [query]);

  const handleSelectSuggestion = (symbol) => {
    setInputSymbol(symbol);
    setQuery(symbol);
    setSuggestions([]);
  };

  return (
    <div>
      <TextField
        fullWidth
        variant="outlined"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        placeholder="Enter Stock Symbol"
      />
      <Popper open={suggestions.length > 0} placement="bottom-start">
        <Paper>
          {loading ? (
            <CircularProgress />
          ) : (
            <List>
              {suggestions.map((suggestion) => (
                <ListItem
                  button
                  key={suggestion.symbol}
                  onClick={() => handleSelectSuggestion(suggestion.symbol)}
                >
                  <ListItemText
                    primary={`${suggestion.symbol} - ${suggestion.description}`}
                  />
                </ListItem>
              ))}
            </List>
          )}
        </Paper>
      </Popper>
    </div>
  );
};

export default Autocomplete;
