// Import dependencies
import React, { useState } from 'react';
import axios from 'axios';
import { Box, Button, Container, Grid, Typography, CircularProgress } from '@mui/material';
import Autocomplete from './AutoComplete';

// Define the StockData component
const StockData = () => {
  const [stockData, setStockData] = useState({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [inputSymbol, setInputSymbol] = useState('');

  // Define your API URL and API key from .env
  const API_URL = 'https://finnhub.io/api/v1/quote'; // Finnhub quote API
  const API_KEY = process.env.REACT_APP_FINNHUB_API_KEY; // Access the API key from .env

  // Function to fetch stock data
  const fetchStockData = async (symbol) => {
    setLoading(true);
    setError(null);
    let stockResults = {};

    try {
      const response = await axios.get(`${API_URL}?symbol=${symbol}&token=${API_KEY}`);
      
      if (response.status === 200 && response.data) {
        const data = response.data;

        stockResults[symbol] = {
          'currentPrice': data.c,
          'high': data.h,
          'low': data.l,
          'open': data.o,
          'previousClose': data.pc,
        };
      } else {
        throw new Error('No data available for the provided symbol.');
      }

      setStockData(stockResults);
    } catch (err) {
      if (err.response && err.response.status === 403) {
        setError('Forbidden: Check your API key and permissions.');
      } else {
        setError(err.message);
      }
    } finally {
      setLoading(false);
    }
  };

  // Function to handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    fetchStockData(inputSymbol);
  };

  return (
    <Container maxWidth="lg">
      <Typography variant="h4" align="center" gutterBottom>
        Stock Market Dashboard
      </Typography>

      <Box component="form" onSubmit={handleSubmit} sx={{ mb: 4 }}>
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} sm={8} md={6}>
            <Autocomplete setInputSymbol={setInputSymbol} />
          </Grid>
          <Grid item xs={12} sm={4} md={2}>
            <Button fullWidth type="submit" variant="contained" color="primary" sx={{ mt: 2 }}>
              Search
            </Button>
          </Grid>
        </Grid>
      </Box>

      {loading && <CircularProgress />}
      {error && <Typography color="error">{error}</Typography>}

      {stockData && (
        <Box>
          <Typography variant="h6">Stock Data:</Typography>
          {Object.keys(stockData).map((symbol) => (
            <Box key={symbol} sx={{ mt: 2 }}>
              <Typography variant="body1">Symbol: {symbol}</Typography>
              <Typography variant="body1">Current Price: ${stockData[symbol].currentPrice}</Typography>
              <Typography variant="body1">High Price: ${stockData[symbol].high}</Typography>
              <Typography variant="body1">Low Price: ${stockData[symbol].low}</Typography>
              <Typography variant="body1">Open Price: ${stockData[symbol].open}</Typography>
              <Typography variant="body1">Previous Close: ${stockData[symbol].previousClose}</Typography>
            </Box>
          ))}
        </Box>
      )}
    </Container>
  );
};

export default StockData;
